html {
	box-sizing: border-box;
}

/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
/* Typography
--------------------------------------------- */
body,
button,
input,
select,
optgroup,
textarea {
	color: #404040;
	font-family: "Open Sans", sans-serif;
	font-size: 1rem;
	line-height: 1.65;
}

h1,
h2,
h3 {
	clear: both;
	font-family: "Roboto", sans-serif;
}

h1 {
	font-size: 2.5rem;
}

h2 {
	font-size: 1.6rem;
}

p {
	margin-bottom: 1.5em;
}

dfn,
cite,
em,
i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", courier, monospace;
	line-height: 1.5;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code,
kbd,
tt,
var {
	font-family: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
}

abbr,
acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark,
ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/* Elements
--------------------------------------------- */
body {
	background: #fff;
	overflow-x: hidden;
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul,
ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: 700;
}

dd {
	margin: 0 1.5em 1.5em;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

img {
	height: auto;
	max-width: 100%;
}

figure {
	margin: 1em 0;
}

table {
	margin: 0 0 1.5em;
	width: 100%;
}

/* Links
--------------------------------------------- */
a {
	color: #4169e1;
	text-decoration: none;
	font-weight: 700;
}
a:hover, a:focus, a:active {
	color: #305c9f;
}
a:focus {
	outline: thin dotted;
}
a:hover, a:active {
	outline: 0;
}

/* Forms
--------------------------------------------- */
button,
input[type=button],
input[type=reset],
input[type=submit] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	border-radius: 3px;
	background: #e6e6e6;
	color: rgba(0, 0, 0, 0.8);
	line-height: 1;
	padding: 0.6em 1em 0.4em;
}
button:hover,
input[type=button]:hover,
input[type=reset]:hover,
input[type=submit]:hover {
	border-color: #ccc #bbb #aaa;
}
button:active, button:focus,
input[type=button]:active,
input[type=button]:focus,
input[type=reset]:active,
input[type=reset]:focus,
input[type=submit]:active,
input[type=submit]:focus {
	border-color: #aaa #bbb #bbb;
}

input[type=text],
input[type=email],
input[type=url],
input[type=password],
input[type=search],
input[type=number],
input[type=tel],
input[type=range],
input[type=date],
input[type=month],
input[type=week],
input[type=time],
input[type=datetime],
input[type=datetime-local],
input[type=color],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
	padding: 3px;
}
input[type=text]:focus,
input[type=email]:focus,
input[type=url]:focus,
input[type=password]:focus,
input[type=search]:focus,
input[type=number]:focus,
input[type=tel]:focus,
input[type=range]:focus,
input[type=date]:focus,
input[type=month]:focus,
input[type=week]:focus,
input[type=time]:focus,
input[type=datetime]:focus,
input[type=datetime-local]:focus,
input[type=color]:focus,
textarea:focus {
	color: #111;
}

select {
	border: 1px solid #ccc;
}

textarea {
	width: 100%;
}

/*--------------------------------------------------------------
# Layouts
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Components
--------------------------------------------------------------*/
/* Header
--------------------------------------------- */
.site-header {
	grid-column: 2;
	background-color: #111;
	color: #fff;
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: fixed;
	padding: 0 1rem;
	right: 0;
	left: 0;
	top: 0;
	font-weight: 700;
	z-index: 1000;
}
.site-header a {
	color: #fff;
}

.logged-in .site-header {
	top: 2rem;
}

.site-branding {
	padding: 0.2rem 0;
	height: 3.5rem;
}

.site-title {
	height: 3.5rem;
	font-size: 1.875rem;
	margin: 0;
	text-transform: uppercase;
}

.site-description {
	margin: 0;
}

@media screen and (min-width: 50em) {
	.site-branding {
		text-align: center;
	}
	.site-header {
		overflow: hidden;
		display: grid;
		grid-template-columns: 1fr minmax(auto, 25rem) minmax(auto, 37.5rem) 1fr;
		justify-content: center;
	}
	.site-branding {
		grid-column: 2/3;
		text-align: left;
	}
}
/* Navigation
--------------------------------------------- */
.main-navigation .menu-main-menu-container {
	background-color: #111;
	position: absolute;
	top: 3.5rem;
	left: 0;
	right: 0;
}
.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
	text-align: center;
}
.main-navigation ul ul {
	background-color: grey;
	position: relative;
	width: 100%;
	z-index: 9999999;
	text-align: center;
}
.main-navigation ul ul ul {
	left: -999em;
	top: 0;
}
.main-navigation ul ul li {
	left: 0;
	right: 0;
}
.main-navigation ul ul a {
	width: 100%;
	padding: 1rem 0;
}
.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
	left: auto;
}
.main-navigation li {
	position: relative;
	text-transform: uppercase;
	font-size: 1.1rem;
}
.main-navigation li:hover, .main-navigation li.focus {
	background-color: #305c9f;
}
.main-navigation a {
	display: block;
	text-decoration: none;
	padding: 1rem;
}
.main-navigation .current_page_item > a,
.main-navigation .current-menu-item,
.main-navigation .current_page_ancestor > a,
.main-navigation .current-menu-ancestor > a {
	background-color: #305c9f;
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}

.menu-toggle {
	background: none;
	border: 0;
	padding: 0.6em 0 0.4em;
}
.menu-toggle svg {
	height: 2.5rem;
	width: 2.5rem;
	fill: #fff;
}

@media screen and (min-width: 48em) {
	.logged-in .sub-menu {
		top: 5.8rem;
	}
	.menu-toggle {
		display: none;
	}
	.main-navigation {
		padding-left: 6rem;
	}
	.main-navigation .menu-main-menu-container {
		position: relative;
		top: 0;
		background-color: transparent;
	}
	.main-navigation ul {
		display: flex;
	}
	.main-navigation ul ul {
		background-color: #111;
		position: fixed;
		top: 3.8rem;
		left: -999rem;
		max-width: 15rem;
		flex-direction: column;
	}
	.main-navigation ul ul li {
		left: 0;
		text-align: left;
		padding-left: 1.5rem;
		font-size: 0.95rem;
	}
}
.site-main .comment-navigation,
.site-main .posts-navigation,
.site-main .post-navigation {
	margin: 0 0 1.5em;
}
.comment-navigation .nav-links,
.posts-navigation .nav-links,
.post-navigation .nav-links {
	display: flex;
}
.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
	flex: 1 0 50%;
}
.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
	text-align: end;
	flex: 1 0 50%;
}

/* Posts and pages
--------------------------------------------- */
.sticky {
	display: block;
}

.updated:not(.published) {
	display: none;
}

.site {
	display: grid;
	grid-template-rows: auto 1fr auto;
	grid-template-columns: minmax(2rem, 1fr) minmax(0, 65rem) minmax(2rem, 1fr);
	overflow: hidden;
}

.skip-link {
	grid-column: 2;
}

.site-main {
	grid-column: 2;
	padding-top: 5rem;
	max-width: 60rem;
	margin: 0 auto;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5rem 0 4rem;
}
.page-content p,
.entry-content p,
.entry-summary p {
	max-width: 60rem;
	margin: 0 auto;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

.schedule {
	width: 100%;
	border-collapse: collapse;
}
.schedule tr:nth-child(even) {
	background-color: #ececec;
}
.schedule tr td {
	padding: 0.4rem;
	border: 2px solid #000;
	font-weight: normal;
}
.schedule tr:first-child td {
	font-weight: bold;
}

@media (min-width: 500px) {
	.schedule tr td {
		padding: 1rem;
	}
}
.news-item {
	max-width: 300px;
	margin: 0 auto;
}
.news-item h3 {
	font-size: 1.25rem;
	margin-top: 0;
}

.wp-container-3 {
	margin-bottom: 4rem;
}

@media (min-width: 900px) {
	.news {
		display: flex;
		justify-content: space-around;
	}
}
.student {
	max-width: 300px;
	margin: 0 auto 2.5rem;
}

@media (min-width: 50em) {
	.student-container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;
	}
}
.single-taxonomy-student {
	margin-bottom: 4rem;
}
.single-taxonomy-student img {
	float: left;
	margin-right: 1rem;
}

@media (min-width: 57em) {
	.single-taxonomy-student {
		display: flex;
		flex-wrap: wrap;
	}
	.single-taxonomy-student h2 {
		width: 100%;
		margin-bottom: 1rem;
	}
	.single-taxonomy-student .single-student-content {
		width: 40rem;
	}
	.single-taxonomy-student .single-student-content p {
		margin-top: 0;
	}
}
.wp-block-button {
	margin-top: 1rem;
}

.dilly-student .entry-content {
	max-width: 22rem;
}

@media (min-width: 60em) {
	.dilly-student {
		display: grid;
		grid-template-columns: minmax(auto, 35rem) 1fr;
		align-items: center;
	}
	.dilly-student img {
		grid-row: 2;
		grid-column: 2;
	}
	.dilly-student .entry-content {
		grid-row: 2;
		grid-column: 1;
		padding-right: 1rem;
		max-width: 45rem;
	}
}
.faculty-member {
	margin-bottom: 3rem;
}
.faculty-member p {
	margin-bottom: 0;
}

.administrative-member {
	margin-bottom: 3rem;
}

@media (min-width: 50em) {
	.administrative {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	.administrative h2 {
		width: 100%;
	}
	.administrative .administrative-member {
		width: 48%;
	}
	.faculty {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	.faculty h2 {
		width: 100%;
	}
	.faculty .faculty-member {
		width: 48%;
	}
}
.post {
	margin-bottom: 4rem;
}
.post img {
	min-height: 300px;
	max-height: 400px;
	object-fit: cover;
}
.post .entry-content {
	margin-bottom: 1.5rem;
}

.cat-links, .tags-links, .comments-links {
	display: block;
}

.type-post {
	max-width: 256px;
	margin: 0 auto;
	margin-bottom: 4rem;
}

@media (min-width: 500px) {
	.type-post {
		max-width: 100%;
	}
}
/* Comments
--------------------------------------------- */
.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}

/* Footer
--------------------------------------------- */
.site-footer {
	grid-column: 1/4;
	background-color: #111;
	color: #fff;
	text-align: center;
	padding: 2rem 2rem 6rem 2rem;
	margin-top: 3rem;
}

.site-footer a {
	color: #fff;
}

.footer-nav {
	margin-top: 1.9rem;
	text-align: center;
}
.footer-nav h3 {
	font-size: 2rem;
	margin-bottom: 1rem;
}

.site-footer a:hover,
.site-footer a:focus,
.site-footer a:active {
	color: #305c9f;
}

.site-footer a:hover path,
.site-footer a:focus path,
.site-footer a:active path {
	fill: #305c9f;
}

.menu-footer-menu-container {
	max-width: 62.5rem;
	margin: 0 auto;
}
.menu-footer-menu-container .menu {
	list-style: none;
	margin: 0;
	padding-left: 0;
}

.site-info {
	text-align: center;
	margin-top: 2rem;
}
.site-info h3 {
	font-size: 2rem;
	margin-bottom: 1rem;
}

.logo {
	max-width: 290px;
}
.logo img {
	max-width: 290px;
}

@media screen and (min-width: 50em) {
	.site-footer {
		text-align: left;
		display: grid;
		grid-template-columns: minmax(auto, 20rem) minmax(auto, 19rem) minmax(auto, 19rem);
		justify-content: center;
		align-items: start;
		justify-items: stretch;
	}
}
/* Widgets
--------------------------------------------- */
.widget {
	margin: 0 0 1.5em;
}
.widget select {
	max-width: 100%;
}

.widget-area {
	grid-column: 2;
	width: 100%;
	margin: 0 auto;
	padding: 0 2rem;
}

.widget_search {
	margin-top: 1.5rem;
}

.wp-block-calendar {
	max-width: 400px;
}

@media (min-width: 60em) {
	.widget {
		max-width: 18rem;
		padding: 0 1rem;
	}
	.widget ul {
		margin-left: 0;
	}
	.widget-area {
		max-width: 68rem;
		display: flex;
		justify-content: space-between;
	}
	.wp-block-search__inside-wrapper {
		flex-wrap: wrap;
	}
	.wp-block-search__inside-wrapper button {
		margin-top: 1rem;
		width: 100%;
		margin-left: 0;
	}
	.wp-block-search__inside-wrapper button:hover,
	.wp-block-search__inside-wrapper button :focus {
		background-color: #305c9f;
		color: white;
	}
}
/* Media
--------------------------------------------- */
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
	display: inline-block;
}

/* Captions
--------------------------------------------- */
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}
.wp-caption img[class*=wp-image-] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}
.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: center;
}

/* Galleries
--------------------------------------------- */
.gallery {
	margin-bottom: 1.5em;
	display: grid;
	grid-gap: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	width: 100%;
}

.gallery-columns-2 {
	grid-template-columns: repeat(2, 1fr);
}

.gallery-columns-3 {
	grid-template-columns: repeat(3, 1fr);
}

.gallery-columns-4 {
	grid-template-columns: repeat(4, 1fr);
}

.gallery-columns-5 {
	grid-template-columns: repeat(5, 1fr);
}

.gallery-columns-6 {
	grid-template-columns: repeat(6, 1fr);
}

.gallery-columns-7 {
	grid-template-columns: repeat(7, 1fr);
}

.gallery-columns-8 {
	grid-template-columns: repeat(8, 1fr);
}

.gallery-columns-9 {
	grid-template-columns: repeat(9, 1fr);
}

.gallery-caption {
	display: block;
}

/*--------------------------------------------------------------
# Plugins
--------------------------------------------------------------*/
/* Jetpack infinite scroll
--------------------------------------------- */
/* Hide the Posts Navigation and the Footer when Infinite Scroll is in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
	display: none;
}

/* Re-display the Theme Footer when Infinite Scroll has reached its end. */
.infinity-end.neverending .site-footer {
	display: block;
}

/*--------------------------------------------------------------
# Utilities
--------------------------------------------------------------*/
/* Accessibility
--------------------------------------------- */
/* Text meant only for screen readers. */
.screen-reader-text {
	border: 0;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute !important;
	width: 1px;
	word-wrap: normal !important;
}
.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	clip-path: none;
	color: #21759b;
	display: block;
	font-size: 0.875rem;
	font-weight: 700;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000;
}

/* Do not show the outline on the skip link target. */
#primary[tabindex="-1"]:focus {
	outline: 0;
}

/* Alignments
--------------------------------------------- */
.alignleft {
	/*rtl:ignore*/
	float: left;
	/*rtl:ignore*/
	margin-right: 1.5em;
	margin-bottom: 1.5em;
}

.alignright {
	/*rtl:ignore*/
	float: right;
	/*rtl:ignore*/
	margin-left: 1.5em;
	margin-bottom: 1.5em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 1.5em;
}

.alignwide,
.alignfull {
	display: block;
	width: 100vw;
	max-width: 100vw;
	margin: 2rem calc(50% - 50vw);
}

.alignwide {
	width: auto;
}

@media only screen and (min-width: 1300px) {
	.alignwide {
		max-width: 1300px;
		margin-left: -150px;
		margin-right: -150px;
	}
}